<template>
	<el-container :style="{height: this.$root.height+'px'}">
		<el-header class="main-header">
			<el-col :span="20" class="main-title">数据可视化</el-col>
			<el-col :span="4" class="main-head-but">
				<el-button type="primary" icon="el-icon-s-operation" @click="drawerShow = true"></el-button>
			</el-col>
		</el-header>
		<div class="current-time"><i class="el-icon-timer">&nbsp;</i>{{currentTime}}</div>
		<el-drawer :with-header="false" :visible.sync="drawerShow" direction="ltr" size="70%">
			<el-menu :default-active="$route.path" router>
				<MainMenu></MainMenu>
			</el-menu>
		</el-drawer>
		<el-container>
			<el-aside class="main-aside">
				<el-menu :default-active="$route.path" :style="{height: this.$root.height-60+'px'}" router>
					<MainMenu></MainMenu>
				</el-menu>
			</el-aside>
			<el-main :style="{height: this.$root.height-60+'px'}" >
				<router-view></router-view>
			</el-main>
		</el-container>
	</el-container>
</template>

<script>
	import MainMenu from '../components/MainMenu'
	export default {
		name: 'Main',
		components: {
			MainMenu
		},
		data() {
			return {
				drawerShow: false,
				currentTime: null
			}
		},
		methods: {
			setTime() {
				let time = new Date();
				this.currentTime = time.toLocaleString();
			},
			setTimer() {
				let timer = setInterval(this.setTime, 1000);
				this.$once("hook:beforeDestroy", () => {
					clearInterval(this.timer);
				});
			}
		},
		mounted() {
			this.setTimer()
		}
	}
</script>

<style>
	.main-header {
		background-color: #409eff;
	}

	.main-title {
		color: white;
		font: italic bold 30px/40px serif;
		margin-top: 10px;
	}

	.main-head-but {
		display: none;
		margin-top: 10px;
		text-align: right;
	}

	.current-time {
		z-index: 9;
		position: absolute;
		top: 60px;
		right: 20px;
		font-size: 15px;
		color: rgba(100,100,100,0.8);
	}

	.main-aside {
		width: 200px !important
	}

	@media screen and (max-width: 700px) {
		.main-head-but {
			display: inline-block;
			text-align: right;
		}

		.main-aside {
			display: none;
		}
	}
</style>
